// No @use "minima/..."; statements here to avoid conflicts.
// All styles are explicitly explicitly defined.

@font-face {
    font-family: 'Inter SemiBold';
    src: url('../fonts/Inter-SemiBold.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Inter Thin';
    src: url('../fonts/Inter-Thin.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

html,
body {
    min-height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Inter SemiBold', sans-serif; /* Apply SemiBold to body */
    color: #000;
}

h1, h2, h3 {
    font-family: 'Inter Thin', sans-serif; /* Apply Thin to titles */
}

/* Default font sizes for all heading types */
h1 {
    font-size: 2.5em; /* Default H1 size */
}

h2 {
    font-size: 2em; /* Default H2 size */
}

h3 {
    font-size: 1.5em; /* Default H3 size */
}

h4 {
    font-size: 1.2em; /* Default H4 size */
}

h5 {
    font-size: 1em; /* Default H5 size */
}

h6 {
    font-size: 0.8em; /* Default H6 size */
}

.container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    position: relative; /* For fixed positioning context */
}

.wrapper {
    max-width: 800px; /* Your desired content width */
    margin: 0 auto; /* Center the wrapper */
    padding: 0 30px; /* Padding on sides for content */
    flex-grow: 1; /* Allows content to take available space */
    padding-top: 150px; /* Space for fixed header */
    padding-bottom: 100px; /* Space for fixed footer */
}

.header {
    position: fixed;
    top: 20px;
    left: 20px;
    z-index: 1000;
}

.header h1 a {
    color: inherit;
    text-decoration: none;
}

html.dark-mode .header h1 a {
    color: #f0f0f0; /* White text for dark mode */
}

.header h1 a:hover {
    color: #555; /* Slightly darker for light mode */
}

html.dark-mode .header h1 a:hover {
    color: #cccccc; /* Slightly lighter for dark mode */
}

.header nav ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 0 0;
}

.header nav ul li {
    display: flex;
    align-items: center;
}

.header nav ul li a {
    color: inherit;
    text-decoration: none;
    padding: 5px 0;
}

html.dark-mode .header nav ul li a {
    color: #f0f0f0; /* White text for dark mode */
}

.header nav ul li a:hover {
    color: #555; /* Slightly darker for light mode */
}

html.dark-mode .header nav ul li a:hover {
    color: #cccccc; /* Slightly lighter for dark mode */
}

.content a {
    color: inherit;
    text-decoration: none;
}

html.dark-mode .content a {
    color: #f0f0f0; /* White text for dark mode */
}

.content a:hover {
    color: #949494; /* Slightly darker for light mode */
}

html.dark-mode .content a:hover {
    color: #b8b8b8; /* Slightly lighter for dark mode */
}

.footer {
    position: fixed;
    bottom: 20px;
    right: 20px;
    text-align: right;
}

.footer p a {
    color: inherit;
    text-decoration: none;
}

.footer p a:hover {
    color: #555; /* Slightly darker for light mode */
}

html.dark-mode .footer p a:hover {
    color: #cccccc; /* Slightly lighter for dark mode */
}

.nav-circle {
    display: inline-block;
    width: 8px;
    height: 8px;
    background-color: #000; /* Black circle for light mode */
    border-radius: 50%;
    margin-right: 5px; /* Adjusted margin to the right */
    vertical-align: middle;
}

html.dark-mode .nav-circle {
    background-color: #fff; /* White circle for dark mode */
}

/* Styles to remove bullet points from general content lists */
.content ul {
    list-style: none;
    padding-left: 0;
}

.content li {
    margin-bottom: 10px; /* Add some spacing between list items */
}

/* Re-enable bullet points for Table of Contents */
h2#table-of-contents + ul {
    list-style: disc; /* Use disc for standard bullet points */
    padding-left: 20px; /* Add back some indentation */
}

/* Centering for programming.html and art.html listing pages */
.centered-content-page .content {
    text-align: center; /* Center inline content like text and images */
}

.centered-content-page .content ul {
    display: table; /* To center the entire list block */
    margin-left: auto;
    margin-right: auto;
    text-align: left; /* Keep text within list items left-aligned */
}

.centered-content-page .content ul li {
    display: flex; /* Use flexbox for side-by-side layout */
    align-items: center; /* Vertically align items in the middle */
    justify-content: space-between; /* Push items to opposite ends */
    text-align: left; /* Reset text alignment for the flex item */
    margin-bottom: 20px; /* Add more space between list items */
}

.centered-content-page .content ul li .excerpt {
    flex-shrink: 0; /* Prevent image from shrinking */
    margin-left: 100px; /* Add a small margin to separate from text */
}

.centered-content-page .content ul li .excerpt img {
    max-width: 100px; /* Adjust image size as needed */
    height: auto;
    display: block; /* Ensure it behaves as a block within flex item */
}

.centered-content-page .content ul li > div:not(.excerpt) {
    /* This targets the div containing the title and other text */
    flex-grow: 1; /* Allow text content to take remaining space */
}

.centered-content-page .content ul li h2,
.centered-content-page .content ul li .excerpt {
    text-align: left; /* Ensure text within list items is left-aligned */
}

/* Increase title size in programming and art sections (overrides defaults) */
.centered-content-page .content h1 {
    font-size: 3em; /* Increased H1 size */
}

.centered-content-page .content h2 {
    font-size: 2.5em; /* Increased H2 size */
}

.centered-content-page .content h3 {
    font-size: 1.8em; /* Increased H3 size */
}

/* Ensure images and iframes are centered within the content */
.content img {
    display: block; /* Make them block-level for margin:auto to work */
    margin-left: auto;
    margin-right: auto;
    max-width: 100%; /* Prevent overflow */
    height: auto; /* Maintain aspect ratio */
}

.content iframe {
    width: 100%;
    height: auto;
    aspect-ratio: 16 / 9; /* For 16:9 aspect ratio */
    display: block; /* Ensure it's block-level for centering */
    margin-left: auto;
    margin-right: auto;
}

/* Make excerpt images in art section smaller */
.centered-content-page .content .excerpt img {
    max-width: 300px; /* Adjust this value to make them smaller */
    height: auto;
}

/* Dark Mode Styles */
html.dark-mode {
    background-color: #1a1a1a; /* Dark background for html element */
}

html.dark-mode body {
    color: #f0f0f0; /* Light text */
}

html.dark-mode .site-header {
    background-color: #2a2a2a;
    border-bottom: 1px solid #3a3a3a;
}

html.dark-mode .site-nav .page-link {
    color: #f0f0f0;
}

html.dark-mode .site-nav .page-link:hover {
    color: #929292;
}

html.dark-mode .post-link {
    color: #f0f0f0;
}

html.dark-mode .post-link:hover {
    color: #929292;
}

html.dark-mode .site-footer {
    background-color: #2a2a2a;
    border-top: 1px solid #3a3a3a;
    color: #f0f0f0;
}

html.dark-mode .header nav ul li a {
    color: #f0f0f0;
}

html.dark-mode .footer a {
    color: #f0f0f0;
}

/* Theme Toggle Styles (Radio Buttons) */
.theme-toggle-wrapper {
    position: fixed;
    bottom: 20px;
    left: 20px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.theme-option {
    display: flex;
    align-items: center;
}

.theme-option input[type="radio"] {
    /* Hide default radio button */
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 12px;
    height: 12px;
    border: 2px solid #333; /* Default outer border (light mode) */
    border-radius: 0; /* Square shape */
    cursor: pointer;
    margin-right: 4px;
    position: relative;
    outline: none;
    background-color: transparent; /* Default background */
}

/* Dark mode specific outer border for radio buttons */
html.dark-mode .theme-option input[type="radio"] {
    border-color: #ccc; /* Lighter border for dark mode */
}

/* Checked state for light mode */
.theme-option input[type="radio"]#light-mode:checked {
    border-color: #000; /* Outer border black */
    background-color: #fff; /* Inner border white */
}

.theme-option input[type="radio"]#light-mode:checked::before {
    background-color: #000; /* Fill black */
    content: '';
    display: block;
    width: 6px; /* Adjust size for fill */
    height: 6px; /* Adjust size for fill */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

/* Checked state for dark mode */
.theme-option input[type="radio"]#dark-mode:checked {
    border-color: #fff; /* Outer border white */
    background-color: #000; /* Inner border black */
}

html.dark-mode .theme-option input[type="radio"]#dark-mode:checked {
    border-color: #fff; /* Outer border white */
    background-color: #000; /* Inner border black */
}

.theme-option input[type="radio"]#dark-mode:checked::before {
    background-color: #fff; /* Fill white */
    content: '';
    display: block;
    width: 6px; /* Adjust size for fill */
    height: 6px; /* Adjust size for fill */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.theme-option label {
    cursor: pointer;
    font-size: 1em;
    color: #000; /* Default light mode text color */
}

/* Dark mode specific styles for labels */
html.dark-mode .theme-option label {
    color: #f0f0f0; /* White text in dark mode */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
}

#holder {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}